import React, { Component } from 'react';
import start_icon from "./static/img/play.svg"
import check_icon from "./static/img/check_blank.svg"
import checked_icon from "./static/img/check_full.svg"
import pause_icon from "./static/img/pause.svg"
import delete_icon from "./static/img/error.svg"

export default class Task extends Component{
  render(){
    const active = this.props.started
    const complete = this.props.done
    if (this.props.edit){
    return (
        <div className="oneTask">
            <img className="leftButton" src={delete_icon} alt={"Delete"} onClick={this.props.onDelete} />
            {complete  ? (<span onClick={this.props.taskSelected}><s> {this.props.name} </s></span>) : (<span onClick={this.props.taskSelected}>{this.props.name}</span>)}
            <img className="rightButton" src={((complete) ? checked_icon : check_icon )} alt={complete ? "Mark not done" : "Mark done"} onClick={this.props.onComplete} />
        </div>
    );
    }
    var shallow = false;
    var deep = false;
    const tags = this.props.tags;
    const len = tags.length;
        for(var j = 0; j < len; j++){
            const t = tags[j];
            if(t.name === "shallow"){
                shallow = true;
            }
            if(t.name === "deep"){
                deep = true;
            }
        }
    if(deep){
    return (
        <div className="oneTask">
            <img className="leftButton" src={((active) ? pause_icon : start_icon )} alt={active ? "Pause" : "Start"} onClick={this.props.onStart} />
            {complete ? (<span onClick={this.props.taskSelected}><s><b> {this.props.name} </b></s></span>) : (<span onClick={this.props.taskSelected}><b>{this.props.name}</b></span>)}
            <img className="rightButton" src={((complete) ? checked_icon : check_icon )} alt={complete ? "Mark not done" : "Mark done"} onClick={this.props.onComplete} />
        </div>
    );
    }
    if(shallow){
    return (
        <div className="oneTask">
            <img className="leftButton" src={((active) ? pause_icon : start_icon )} alt={active ? "Pause" : "Start"} onClick={this.props.onStart} />
            {complete ? (<span onClick={this.props.taskSelected}><s><i> {this.props.name} </i></s></span>) : (<span onClick={this.props.taskSelected}><i>{this.props.name}</i></span>)}
            <img className="rightButton" src={((complete) ? checked_icon : check_icon )} alt={complete ? "Mark not done" : "Mark done"} onClick={this.props.onComplete} />
        </div>
    );
    }
    return (
        <div className="oneTask">
            <img className="leftButton" src={((active) ? pause_icon : start_icon )} alt={active ? "Pause" : "Start"} onClick={this.props.onStart} />
            {complete  ? (<span onClick={this.props.taskSelected}><s> {this.props.name} </s></span>) : (<span onClick={this.props.taskSelected}>{this.props.name}</span>)}
            <img className="rightButton" src={((complete) ? checked_icon : check_icon )} alt={complete ? "Mark not done" : "Mark done"} onClick={this.props.onComplete} />
        </div>
    );
  }

}
